import React from "react";
import "../css/login.css";
// import "../css/signup.css";
// import "../css/calculator.css";
import { useForm } from "react-hook-form";
import Swal from 'sweetalert2'

const Login = () => {
  const { register, handleSubmit,formState:{errors} } = useForm();
  const onsubmit = (data) => {
    console.log(data);
    const Toast = Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.onmouseenter = Swal.stopTimer;
        toast.onmouseleave = Swal.resumeTimer;
      }
    });
    Toast.fire({
      icon: "success",
      title: "Signed in successfully"
    });
  };
     return (
    <div id="login">
      <form onSubmit={handleSubmit(onsubmit)}>
        <div class="welcome">
          <label class="required">
            <i class="fa-solid fa-user"></i> Username
          </label>
          <input type="text" maxlength="16" {...register("username",{
            required:{
              value:true,
              message:'username is required'
            },
            minLength: {
              value:3,
              message:'username should have atleast 3 character'
            },
            maxLength:{
              value:15,
              message: 'username should have atleast 10 character'
            }
          })} />
          <small>{errors.username?.message}</small>
        </div>
        <div class="welcome">
          <label class="required">
            {" "}
            <i class="fa-solid fa-key"></i> password
          </label>
          <input type="text" max="15" {...register("pasword",{
            pattern:{
              value:/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,10}$/,
              message:"Minimum eight and maximum 10 characters, at least one uppercase letter, one lowercase letter, one number and one special character"
            }
          })} />
          <small>{errors.username?.message}</small>
          
        </div>
        <div class="welcome">
          <button>
            Login <i class="fa-solid fa-right-to-bracket fa-beat"></i>
          </button>
        </div>
      </form>
    </div>
  );
};

export default Login;
