import React from "react";
import profile from "../assets/user.png";
import "../css/emplist.css";

const Card = (Props) => {
  return (
    <div className="card">
      <img src={profile} alt="images" width="100" height="100" />

      <div className="table">
        <p>
          Username :<span>{Props.emp.Name}</span>
        </p>
        <p>
          Id No :<span>{Props.emp.EmployeeID}</span>
        </p>
      </div>
      <button onClick={() => Props.btnfn()} id="view-btn">
        {Props.btntext}
      </button>
    </div>
  );
};
export default Card;
