import React from "react";
import "../css/signup.css"
import {useForm} from "react-hook-form";


const Signup = () => {
  const{register,handleSubmit,formState:{errors}} = useForm();
  const onSubmit =(data) => {
    console.log(data);
  };
  return (
    <div id="signupform">
      <form onSubmit = {handleSubmit(onSubmit)}>
      
      <div class="welcome">
        <label class="required"><i class="fa-solid fa-user"></i> Username</label>
        <input type="text" maxlength="15"  {...register("username",{
          required:{
            value:true,
            message:'username is required'
          },
          minLength: {
            value:3,
            message:'username should have atleast 3 characters'
          },
          maxLength: {
            value:15,
            message: 'username should have atleast 10 characters'
          }
        })}/>
          <small>{errors.username?.message}</small>

      </div>
      <div class="welcome">
        <label class="required"><i class="fa-solid fa-key"></i> Password</label>
        <input type="password" maxlength="10" {...register("password",{
           pattern:{
            value:/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,10}$/,
            message:"Minimum eight and maximum 10 characters, at least one uppercase letter, one lowercase letter, one number and one special character"
          }
        })}/>
          <small>{errors.username?.message}</small>

      </div>
      <div class="welcome">
        <label class="required"><i class="fa-solid fa-key"></i> Re-type password</label>
        <input class="password" maxlength="10" {...register("re-type password",{
           pattern:{
            value:/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,10}$/,
            message:"Minimum eight and maximum 10 characters, at least one uppercase letter, one lowercase letter, one number and one special character"
          }
        })}/>
          <small>{errors.username?.message}</small>

      </div>
      <div class="welcome">
       <button>Signup <i class="fa-solid fa-right-to-bracket fa-beat"></i></button>
      </div>
      </form>
    </div>
  );
};
export default Signup;
