import React from "react";
import "../css/calculator.css";

const Calculator = () => {
  const[value,setValue]= React.useState('')

  return (
    <div id="calculator">
      <input value={value} type="text" placeholder="0" id="result" disabled />
      <div class="unique">
        <button onclick="Allclear()" onClick={() => setValue('')}><i class="fa-solid fa-trash "></i></button>
        <button onclick="Del()">={() => value.slice()}<i class="fa-solid fa-eraser "></i></button>
        <button onclick="display('%')"><i class="fa-solid fa-percent "></i></button>
        <button onclick="display('/')">/</button>
      </div>
      <div class="unique">
      <button onClick={() => setValue(value + '7')}> 7</button>
        
        <button onClick={() => setValue(value + '8')}> 8</button>
        
        <button onClick={() => setValue(value + '9')}> 9</button>
       
        <button onClick={() => setValue(value + '*')}> *</button>
        
      </div>
      <div class="unique">
      <button onClick={() => setValue(value + '4')}> 4</button>
        
        <button onClick={() => setValue(value + '5')}> 5</button>
        
        <button onClick={() => setValue(value + '6')}> 6</button>
        
        <button onClick={() => setValue(value + '_')}> _</button>
        
      </div>
      <div class="unique">
      <button onClick={() => setValue(value + '3')}> 3</button>
        
        <button onClick={() => setValue(value + '2')}> 2</button>
        
        <button onClick={() => setValue(value + '1')}> 1</button>
        <button onClick={() => setValue(value + '+')}> +</button>
        
      </div>
      <div class="unique">
      <button onClick={() => setValue(value + '0')}> 0</button>
        
      <button onClick={() => setValue(value + '.')}> .</button>
        
        <button onClick={() => setValue(eval(value))} className="eq">
          
        =</button>
      </div>
    </div>
  );
};
export default Calculator;
